<template>
  <div>
    <b class="hr"></b>
    <el-button @click="get"></el-button>
    <VueWeb/>
    <div class="body"></div>
    <el-backtop target=".page-component__scroll .el-scrollbar__wrap"></el-backtop>
  </div>
</template>

<script>
import VueWeb from './components/VueWeb.vue'

export default {
  name: 'App',
  components: {
    VueWeb
  },
  methods: {
    get() {
      const loading = this.$loading({
        lock: true,
        text: '拼命加载中，请稍后......',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      setTimeout(() => {
        loading.close();
        this.$message({
          showClose: true,
          message: '恭喜你，发现了这个按钮',
          type: 'success'
        });
      }, 3000);
    }
  }
}
</script>

