import VueRouter from 'vue-router'

export default new VueRouter({
    routes:[
        {path:'/',redirect:'/index'},
        {path:'/index',component:() => import('@/pages/IndexWeb')},
        {path:'/tool',component:() => import('@/pages/ToolWeb')},
        {path:'/page',component:() => import('@/pages/PageWeb')},
    ]
})